
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
import {
	mapMutations,
	mapState
} from 'vuex';
export default {
	computed: {
		...mapState(['token'])
	},
	methods: {
		...mapMutations(['login', 'can_buy_change', 'set_wx_userinfo','set_buy_time','set_token']),
	},
	onLaunch: function() {
		let that = this;
		let local_token=uni.getStorageSync('local_token') || '';
		if(local_token!=''){
			this.set_token(local_token);
			uni.redirectTo({
				url:'pages/index/index'
			})
		}
	},
	onShow: function() {},
	onHide: function() {
	}
}
